<template>
  <div>
    <div class="newproject">
      <mt-header title="工程案例" id="header">
        <router-link to="/" slot="left" >
          <mt-button icon="back"></mt-button>
        </router-link>
      </mt-header>
    </div>
    <div class="com">
      <div class="wraper01" v-for="val in arr">
        <div class="wrap01">
          <img :src="val.img" >
          <p v-text="val.title">项目名称:马里巴马科航站楼</p>
          <p  v-text="val.content">施工内容：铝板幕墙、玻璃幕墙</p>
          <p v-text="val.site">工程地点：马里共和国</p>
        </div>
      </div>
    </div>

  </div>

</template>

<script>
  import axios from 'axios'
    export default {
        name: "newproject",
      data(){
       return{
         arr:[ ]
       }
      },
      methods:{
        getdata(){
          let that = this;
          axios.get('/api/projects/get')
            .then(function (response) {
              that.arr = response.data.data
              console.log(that.arr)
            })
            .catch(function (error) {
              console.log(error);
            });
        }
      },
      mounted() {
        this.getdata()
      }


    }
</script>

<style scoped>
  .newproject{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
  }
  .com{
    margin-top: 50px;
    margin-bottom: 50px;
  }
.wraper01{
  height: 230px;
  border-bottom: 2px solid #ccc;

}
  .wrap01{
    width: 340px;
    height: 230px;
    margin: 15px auto 0;
    background: #fff;

  }
.wrap01 img{
  width: 100%;
  height: 150px;
}
  .wrap01 p{
    color: #484848;
    padding: 5px;
  }
</style>



